<style lang="scss">
    .vacancies {
        &-radial {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width: 565px;
            height: 565px;
            border: 45px solid $blue;
            border-radius: 50%;
            margin-left: auto;
            padding: 50px;
            text-align: center;
            &__text {
                & p {
                    margin-bottom: 0;
                }
                @media (max-width: 991px) {
                    & p {
                        font-size: 13px;
                        line-height: 17px;
                    }
                    & h6 {
                        font-size: 14px;
                        line-height: 18px;
                    }
                }
            }
            @media (max-width: 1240px) {
                width: 465px;
                height: 465px;
            }
            @media (max-width: 991px) {
                width: 315px;
                height: 315px;
                border: 20px solid $blue;
                margin-right: auto;
                margin-top: 40px;
                padding: 40px;
            }
            & ul {
                list-style: none;
                padding: 0;
                margin: 0;
                & li {
                    position: absolute;
                    width: 115px;
                    height: 115px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    background: $white;
                    border: 1px solid $blue;
                    transition: all 0.2s;
                    cursor: pointer;
                    @media (max-width: 1240px) {
                        width: 105px;
                        height: 105px;
                    }
                    @media (max-width: 991px) {
                        width: 80px;
                        height: 80px;
                        & svg {
                            width: 48px;
                            height: 48px;
                        }
                    }
                    &:hover,
                    &.active {
                        background: $orange;
                        border: 1px solid $orange;
                        & svg {
                            & path {
                                fill: $white;
                            }
                        }
                    }
                    &.charity {
                        top: -10%;
                        left: 10%;
                    }
                    &.idea {
                        top: -10%;
                        right: 10%;
                    }
                    &.social {
                        bottom: 10%;
                        right: -10%;
                    }
                    &.party {
                        bottom: -15%;
                        right: 0;
                        left: 0;
                        margin: auto;
                    }
                    &.book {
                        bottom: 10%;
                        left: -10%;
                    }
                }
            }
        }
    }
    .wrapper {
        &::after, 
        &::before {
            display: none;
        }
    }
</style>